import React from 'react'

import Container from '../components/container'
import Head from '../components/head'
import Fade from 'react-reveal/Fade'
import { Link } from 'gatsby'
import { graphql } from 'gatsby'
import Img from 'gatsby-image'

import '../styles/blog.css'

class BlogPage extends React.Component {
    render() {
        const { data } = this.props
        const posts = data.allMarkdownRemark.edges

        return(
            <Container>
                <Head title = "Blog"/>

                <Fade>
                    <h2 className="blogTitle">
                        Create. Blog.
                    </h2>
                </Fade>

                <div className="articlePost">
                    {
                        posts.map(({ node }) => {
                            const title = node.frontmatter.title || node.fields.slug

                            return (
                                <Link to={node.fields.slug}
                                    style={{ textDecoration:"none" }}>

                                    <article key={node.fields.slug} className="articleCont">

                                        <div className="articleAbout">
                                            <h3>{title}</h3>
                                            <p
                                                dangerouslySetInnerHTML={{
                                                    __html: node.frontmatter.description || node.excerpt,
                                                }}
                                            />
                                            <small>{node.frontmatter.duration}</small><br/>
                                            <small>{node.frontmatter.date}</small>                                    
                                        </div>
                                        
                                        <div className="imgContBL">
                                            {!!node.frontmatter.featuredImage && !!node.frontmatter.featuredImage.childImageSharp
                                                ? <Img fluid={node.frontmatter.featuredImage.childImageSharp.fluid}
                                                alt={"Featured"} />
                                                : <img src={node.frontmatter.featuredImage} alt={"Featured"} />
                                            }
                                        </div>

                                    </article>
                                    
                                </Link>
                            )
                        })
                    }
                </div>

            </Container>
        )
    }
}

export default BlogPage

export const postsQuery = graphql`
    query {
        allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
            edges {
                node {
                    excerpt
                    fields {
                        slug
                    }
                    frontmatter {
                        date(formatString: "MMMM DD, YYYY")
                        title
                        description
                        duration
                        caption
                        featuredImage {
                            childImageSharp {
                                fluid(maxWidth: 520) {
                                    ...GatsbyImageSharpFluid
                                }
                            }
                        }
                    }
                }
            }
        }
    }
`